import 'veui-theme-dux/components/button-group.less';
import 'veui-theme-dux/components/button.less';
export default {
  name: 'button-group-demo',
  components: {
    'veui-button': Button,
    'veui-button-group': ButtonGroup,
    'veui-checkbox': Checkbox
  },
  data: function data() {
    return {
      disabled: false,
      items1: [{ label: '新建', event: 'add' }, { label: '编辑', event: 'edit' }, { label: '删除', event: 'remove' }],
      items2: [{ label: '新建', event: 'add', disabled: true }, { label: '编辑', event: 'edit' }, { label: '删除', event: 'remove' }],
      items3: [{ label: '新建', event: 'add', disabled: true }, { label: '编辑', event: 'edit' }, { label: '删除', event: 'remove' }],
      items4: [{ label: '新建', event: 'add', disabled: true }, { label: '编辑', event: 'edit' }, { label: '删除', event: 'remove' }]
    };
  }
};


