

import Icon from '../Icon';
import 'veui-theme-dux/components/icon.less';
import '../../icons';

export default {
  name: 'veui-table-sorter',
  components: {
    'veui-icon': Icon
  },
  props: {
    order: [String, Boolean]
  },
  methods: {
    sort: function sort() {
      var order = void 0;
      if (!this.order) {
        order = 'asc';
      } else {
        order = this.order === 'asc' ? 'desc' : 'asc';
      }
      this.$emit('sort', order);
    }
  }
};


