

import { Dialog, Button, Icon } from 'veui';
import 'veui-theme-dux/components/icon.less';
import 'veui-theme-dux/components/button.less';
import 'veui-theme-dux/components/dialog.less';
import 'veui/icons';
import alertManager from 'veui/managers/alert';
import confirmManager from 'veui/managers/confirm';
import promptManager from 'veui/managers/prompt';
import toastManager from 'veui/managers/toast';

export default {
  name: 'dialog-demo',
  components: {
    Icon: Icon,
    'veui-dialog': Dialog,
    'veui-button': Button
  },
  data: function data() {
    return {
      modalDialogVisible: false,
      nonModalDialogVisible: false,

      draggableDialog1Visible: false,
      draggableDialog2Visible: false,

      operationDialogVisible: false,

      customTextTitleDialogVisible: false,
      customIconTitleDialogVisible: false,

      customContentDialogVisible: false,

      contentAutoHeightDialogVisible: false,
      veryLongDialogVisible: false,

      dynamicContent: '',

      test: '123'
    };
  },
  created: function created() {
    var _this = this;

    setInterval(function () {
      _this.dynamicContent += Date.now() + '<br>';
    }, 1000);
  },

  watch: {
    contentAutoHeightDialogVisible: function contentAutoHeightDialogVisible(value) {
      if (value) {
        this.dynamicContent = '';
      }
    }
  },
  methods: {
    handleOk: function handleOk() {
      alert('点击了确定按钮');
    },
    handleCancel: function handleCancel() {
      alert('点击了取消按钮');
    },
    popupAlerts: function popupAlerts() {
      alertManager.success('成功了', '成功标题', {
        ok: function ok() {
          alert('祝贺你成功了！但是这个对话框并不会马上关闭，而是在三秒之后隐藏');
          return new Promise(function (resolve) {
            setTimeout(resolve, 3000);
          });
        }
      });
      alertManager.info('提示信息', '提示标题');
      alertManager.error('出错了', '出错标题');
      alertManager.warn('警告', '警告');
    },
    popupConfirms: function popupConfirms() {
      confirmManager.warn('真的要删除吗？删除之后不能恢复！', '确认一下', {
        ok: function ok() {
          alert('阻止关闭');
          return true;
        }
      }).then(function (isOk) {
        if (isOk) {
          alert('原来你真的想要删除！');
        }
      });
    },
    popupToasts: function popupToasts() {
      var counter = 1;
      setInterval(function () {
        counter++;
        var type = ['error', 'info', 'success'][counter % 3];
        toastManager[type](type + '-' + counter);
      }, 1000);
    },
    popupPrompt: function popupPrompt() {
      promptManager.info('content', 'title', {
        content: 'content',
        title: 'title'
      }).then(function (_ref) {
        var isOk = _ref.isOk,
            value = _ref.value;

        if (isOk) {
          alert(value);
        }
      });
    }
  }
};


