import _extend from 'lodash/extend';
import _pick from 'lodash/pick';
import _get from 'lodash/get';

import Field from './Field';
import 'veui-theme-dux/components/field.less';

export default {
  name: 'veui-fieldset',
  uiTypes: ['fieldset', 'form-container'],
  components: {
    'veui-field': Field
  },
  props: {
    label: String,
    name: String,
    tip: String,
    disabled: Boolean,
    readonly: Boolean,

    required: false
  },
  computed: {
    attrs: function attrs() {
      return _extend(_pick(this.$props, ['label', 'name', 'tip']), {
        disabled: this.realDisabled,
        readonly: this.realReadonly
      });
    },
    realDisabled: function realDisabled() {
      return this.disabled || _get(this, '$refs.field.form.disabled');
    },
    realReadonly: function realReadonly() {
      return this.readonly || _get(this, '$refs.field.form.readonly');
    }
  }
};


