

import bus from '../bus';
import { Alert, Dialog } from 'veui';

import 'veui-theme-dux/components/dialog.less';
import 'veui-theme-dux/components/alert.less';
export default {
  name: 'alert',
  components: {
    'veui-alert': Alert,
    'veui-dialog': Dialog
  },
  data: function data() {
    return {
      open: false,
      messages: ['我是消息1我是消息1我是消息1我是消息1', '我是消息2', '我是消息3', '我是消息4', '我是消息5']
    };
  },

  methods: {
    close: function close() {
      this.open = true;
    }
  },
  mounted: function mounted() {
    this.$children.forEach(function (child) {
      child.$on('click', function () {
        bus.$emit('log', child.$el.getAttribute('ui'));
      });
    });
  }
};


