

import Icon from '../Icon';
import 'veui-theme-dux/components/icon.less';
import '../../icons';

export default {
  name: 'veui-option',
  components: {
    Icon: Icon
  },
  props: {
    label: [String, Number],
    value: [String, Number],
    disabled: {
      type: Boolean,
      default: false
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    select: function select() {
      if (!this.disabled) {
        this.$emit('select', this.value);
      }
    }
  }
};


