import _findIndex from 'lodash/findIndex';
import _includes from 'lodash/includes';


import { input, ui } from '../mixins';

import Button from './Button';

import 'veui-theme-dux/components/button.less';
export default {
  name: 'veui-check-button-group',
  components: {
    'veui-button': Button
  },
  mixins: [input, ui],
  model: {
    event: 'change'
  },
  props: {
    ui: String,
    items: Array,
    value: Array
  },
  computed: {
    buttonUI: function buttonUI() {
      var allowed = ['alt', 'tiny', 'small', 'large'];
      return this.uiProps.filter(function (ui) {
        return _includes(allowed, ui);
      }).join(' ');
    }
  },
  methods: {
    handleChange: function handleChange(val) {
      if (!_includes(this.value, val)) {
        this.value.push(val);
      } else {
        this.value.splice(_findIndex(this.value, function (item) {
          return item === val;
        }), 1);
      }
      this.$emit('change', this.value);
    }
  }
};


