

import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import Link from './Link';

export default {
  name: 'veui-steps',
  components: {
    'veui-icon': Icon,
    'veui-link': Link
  },
  props: {
    steps: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    current: {
      type: Number,
      default: 0
    }
  }
};


