import _pick from 'lodash/pick';


import { input } from '../mixins';


export default {
  name: 'veui-radio',
  mixins: [input],
  props: {
    ui: String,
    value: null,
    checked: Boolean
  },
  model: {
    prop: 'checked',
    event: 'change'
  },
  computed: {
    attrs: function attrs() {
      var attrs = _pick(this.$props, 'checked');
      attrs.name = this.realName;
      attrs.disabled = this.realDisabled || this.realReadonly;
      return attrs;
    }
  }
};


