

import { Breadcrumb, Icon } from 'veui';
import 'veui-theme-dux/components/icon.less';
import 'veui-theme-dux/components/breadcrumb.less';
import 'veui/icons';

export default {
  name: 'breadcrumb-demo',
  components: {
    'veui-breadcrumb': Breadcrumb,
    'veui-icon': Icon
  },
  data: function data() {
    return {
      items: [{ to: 'http://www.baidu.com', text: 'baidu', native: true }, { to: '/steps', text: '步骤条组件' }, { text: '监听跳转事件', native: true }, { to: 'http://www.baidu.com', text: '阻止跳转事件', native: true }, { to: 'http://www.baidu.com', text: 'replace', replace: true, native: true }, { to: '/dialog', text: 'Dialog' }, { to: 'http://jn.baidu.com', text: 'jn' }]
    };
  },

  methods: {
    handleRedirect: function handleRedirect(event, router, index) {
      if (index === 2) {
        alert('redirect event');
      } else if (index === 3) {
        event.preventDefault();
        alert('prevent redirect event');
      }
    }
  }
};


