import _includes from 'lodash/includes';

import Link from './Link';

var ALLOWED_LINK_TYPES = ['link', 'text'];

export default {
  name: 'veui-breadcrumb-item',
  components: {
    'veui-link': Link
  },
  props: {
    to: {
      type: String,
      default: ''
    },

    replace: Boolean,
    type: {
      type: String,
      default: 'link',
      validator: function validator(value) {
        return _includes(ALLOWED_LINK_TYPES, value);
      }
    },
    native: Boolean
  }
};


