

import bus from '../bus';
import { Icon, Select, Option, Radio } from 'veui';
import 'veui-theme-dux/components/radio.less';
import 'veui-theme-dux/components/option.less';
import 'veui-theme-dux/components/select.less';
import 'veui-theme-dux/components/icon.less';
import type from 'veui/managers/type';
import 'vue-awesome/icons/eye';
import 'vue-awesome/icons/gift';

export default {
  name: 'select-demo',
  components: {
    'veui-select': Select,
    'veui-option': Option,
    'radio': Radio,
    'icon': Icon
  },
  data: function data() {
    return {
      disabled: true,
      selected: true,
      icon: true,
      defaultValue1: null,
      defaultValue2: null,
      defaultValue3: null,
      defaultValue4: null,
      defaultValue5: null,
      defaultValue6: null,
      defaultValue7: null,
      defaultValue8: '1-1',
      defaultValue9: null,
      attrs: {
        name: 'age',
        readonly: false,
        disabled: false,
        selecticon: false,
        options: [{
          label: '下拉选项1',
          value: 1
        }, {
          label: '下拉选项下拉选项下拉选项22',
          value: 2
        }, {
          label: '下拉选项3',
          value: 3
        }, {
          label: '下拉选项4',
          value: 4
        }]
      },
      optGroupAttrs: {
        ui: 'aux',
        name: 'name',
        readonly: false,
        disabled: false,
        selecticon: false,
        options: [{
          label: '下拉选项1',
          options: [{
            label: '子选项1',
            value: '1-1'
          }, {
            label: '子选项子选项子选项子选项子选项子选项2',
            value: '1-2'
          }, {
            label: '子选项3',
            value: '1-3'
          }]
        }, {
          label: '下拉选项下拉选项下拉选项2',
          options: [{
            label: '子选项1',
            value: '2-1'
          }, {
            label: '子选项2',
            value: '2-2'
          }, {
            label: '子选项子选项子选项子选项子选项子选项3',
            value: '2-3'
          }]
        }, {
          label: '下拉选项3',
          options: [{
            label: '子选项1',
            value: '3-1'
          }, {
            label: '子选项2',
            value: '3-2'
          }, {
            label: '子选项子选项子选项子选项子选项子选项3',
            value: '3-3'
          }]
        }, {
          label: '下拉选项4',
          options: [{
            label: '子选项子选项子选项子选项子选项子选项1',
            value: '4-1'
          }, {
            label: '子选项2',
            value: '4-2'
          }, {
            label: '子选项3',
            value: '4-3'
          }]
        }]
      }
    };
  },

  computed: {
    groupedOpts: function groupedOpts() {
      return type.clone(this.optGroupAttrs.options).map(function (group) {
        delete group.label;
        return group;
      }).reduce(function (acc, cur) {
        acc.push(cur);
        return acc;
      }, []);
    }
  },
  mounted: function mounted() {
    this.$children.forEach(function (child) {
      child.$on('click', function () {
        bus.$emit('log', child.$el.getAttribute('ui'));
      });
    });
  },

  methods: {
    change: function change(value) {}
  }
};


