import _toConsumableArray from 'babel-runtime/helpers/toConsumableArray';
import Vue from 'vue';
import Router from 'vue-router';
import routes from './cases';

Vue.use(Router);

export default new Router({
  routes: [{
    path: '/',
    name: 'Empty',
    component: {
      name: 'empty',
      render: function render(h) {
        return h('article', [h('h1', 'Welcome to VEUI!'), h('p', '← Select a demo from the nav bar to get started.')]);
      }
    }
  }].concat(_toConsumableArray(routes))
});


