import _includes from 'lodash/includes';


import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';
import Button from './Button';
import 'veui-theme-dux/components/button.less';
import Overlay from './Overlay';
import 'veui-theme-dux/components/overlay.less';
import { ui, dropdown } from '../mixins';


export default {
  name: 'veui-dropdown',
  components: {
    'icon': Icon,
    'veui-button': Button,
    'veui-overlay': Overlay
  },
  mixins: [ui, dropdown],
  props: {
    ui: String,
    label: String,
    disabled: {
      type: Boolean,
      default: false
    },
    icon: String,
    options: Array
  },
  data: function data() {
    return {
      overlay: {
        attachment: 'top left',
        targetAttachment: 'bottom left',
        constraints: [{
          to: 'scrollParent',
          attachment: 'together'
        }]
      }
    };
  },

  computed: {
    realIcon: function realIcon() {
      if (this.icon) {
        return this.icon;
      }
      if (_includes(this.uiProps, 'link')) {
        return 'triangle';
      }
      return 'angle';
    }
  },
  methods: {
    handleClick: function handleClick(option) {
      if (option.disabled) {
        return;
      }
      this.expanded = false;
      this.$emit('click', option.value);
    }
  }
};


