import _pick from 'lodash/pick';


import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';
import { input } from '../mixins';

import { patchIndeterminate } from '../utils/dom';

export default {
  name: 'veui-checkbox',
  components: {
    Icon: Icon
  },
  mixins: [input],
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    ui: String,
    trueValue: {
      type: null,
      default: true
    },
    falseValue: {
      type: null,
      default: false
    },
    checked: null,
    indeterminate: Boolean
  },
  data: function data() {
    return {
      localIndeterminate: this.indeterminate
    };
  },

  computed: {
    attrs: function attrs() {
      var attrs = _pick(this.$props, 'checked');
      attrs.name = this.realName;
      attrs.disabled = this.realDisabled || this.realReadonly;
      return attrs;
    }
  },
  methods: {
    handleChange: function handleChange(checked) {
      this.localIndeterminate = false;
      this.$emit('update:indeterminate', false);
      this.$emit('change', checked ? this.trueValue : this.falseValue);
    },
    activate: function activate() {
      this.handleChange(!this.checked);
    }
  },
  watch: {
    indeterminate: function indeterminate(value) {
      this.localIndeterminate = value;
      this.$refs.box.indeterminate = value;
    }
  },
  mounted: function mounted() {
    var box = this.$refs.box;
    box.indeterminate = this.localIndeterminate;
    patchIndeterminate(box);
  }
};


