

import { Uploader, Button } from 'veui';
import 'veui-theme-dux/components/button.less';
import 'veui-theme-dux/components/uploader.less';
import { ui } from 'veui/mixins';

export default {
  name: 'uploader-demo',
  components: {
    'veui-uploader': Uploader,
    'veui-button': Button
  },
  data: function data() {
    return {
      uploaderType: 'image',
      uploaderTypeIframe: 'file',
      ui: 'multiline horizontal bottom-mask list-icon',
      uiIframe: 'multiline horizontal',
      needButton: false,
      previewImage: true,
      previewImageIframe: true,
      uploadingContent: 'progressPercent',
      files: [{
        name: 'demo-file1.jpg',
        fileUid: '123456',
        size: '200kb',
        src: 'https://www.baidu.com/img/bd_logo1.png'
      }, {
        name: 'demo-file2.gif',
        fileUid: '222333',
        size: '350kb',
        src: 'http://images.nvidia.com/graphics-cards/geforce/pascal/cn/images/1080-ti-design.png'
      }],
      filesIframe: [{
        name: 'demo-file1.jpg',
        fileUid: '123456',
        size: '200kb',
        src: 'https://www.baidu.com/img/bd_logo1.png'
      }, {
        name: 'demo-file2.gif',
        fileUid: '222333',
        size: '350kb',
        src: 'http://images.nvidia.com/graphics-cards/geforce/pascal/cn/images/1080-ti-design.png'
      }],
      payload: {
        year: '2017',
        month: '4'
      }
    };
  },
  computed: {
    uiPropsIframe: function uiPropsIframe() {
      if (!this.uiIframe || !this.uiIframe.trim()) {
        return [];
      }
      return this.uiIframe.trim().split(/\s+/);
    }
  },
  mixins: [ui],
  methods: {
    onSuccess: function onSuccess(data) {
      console.log(data);
    },
    onFailure: function onFailure(data) {
      console.log(data);
    },
    removeFile: function removeFile(file) {
      this.files = this.files.filter(function (item) {
        return item.name !== file.name;
      });
    },
    removeFileIframe: function removeFileIframe(file) {
      this.filesIframe = this.filesIframe.filter(function (item) {
        return item.name !== file.name;
      });
    },
    cancelUploading: function cancelUploading(file) {
      file.xhr.abort();
      this.removeFile(file);
    },
    cancelUploadingIframe: function cancelUploadingIframe() {
      this.filesIframe.pop();
    },
    toggleUploaderType: function toggleUploaderType() {
      var iframe = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';

      this['uploaderType' + iframe] = this['uploaderType' + iframe] === 'image' ? 'file' : 'image';
    },
    toggleAlign: function toggleAlign() {
      var iframe = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';

      var index = this['uiProps' + iframe].indexOf('horizontal');
      var ui = 'ui' + iframe;
      if (index > -1) {
        this[ui] = this[ui].replace('horizontal', '');
      } else {
        this[ui] += ' horizontal';
      }
    },
    togglePreview: function togglePreview() {
      var iframe = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '';

      this['previewImage' + iframe] = !this['previewImage' + iframe];
    },
    toggleMaskType: function toggleMaskType() {
      if (this.uiProps.indexOf('bottom-mask') > -1) {
        this.ui = this.ui.replace('bottom-mask', '');
      } else {
        this.ui += ' bottom-mask';
      }
    },
    toggleNeedButton: function toggleNeedButton() {
      this.needButton = !this.needButton;
    },
    changeUploadingContent: function changeUploadingContent(type) {
      this.uploadingContent = type;
    }
  }
};


