

import { Form, Fieldset, Field, Label, Span, Input, Button, DatePicker, Uploader, Select, Checkbox, CheckboxGroup, RadioGroup, Searchbox } from 'veui';
import 'veui-theme-dux/components/searchbox.less';
import 'veui-theme-dux/components/radio-group.less';
import 'veui-theme-dux/components/checkbox-group.less';
import 'veui-theme-dux/components/checkbox.less';
import 'veui-theme-dux/components/select.less';
import 'veui-theme-dux/components/uploader.less';
import 'veui-theme-dux/components/date-picker.less';
import 'veui-theme-dux/components/button.less';
import 'veui-theme-dux/components/input.less';
import 'veui-theme-dux/components/field.less';
import 'veui-theme-dux/components/fieldset.less';
import 'veui-theme-dux/components/form.less';
import moment from 'moment';
import bus from '../bus';

export default {
  name: 'demo-form',

  components: {
    'veui-span': Span,
    'veui-input': Input,
    'veui-button': Button,
    'veui-form': Form,
    'veui-fieldset': Fieldset,
    'veui-field': Field,
    'veui-label': Label,
    'veui-datepicker': DatePicker,
    'veui-uploader': Uploader,
    'veui-select': Select,
    'veui-checkbox': Checkbox,
    'veui-checkboxgroup': CheckboxGroup,
    'veui-radiogroup': RadioGroup,
    'veui-searchbox': Searchbox
  },

  data: function data() {
    var habit = ['🏸'];
    var habitItems = [{
      value: '⚽️', label: '足球'
    }, {
      value: '🏀', label: '篮球'
    }, {
      value: '🏸', label: '羽毛球'
    }, {
      value: '🎾', label: '网球'
    }];
    var phoneType = 'mobile';
    var phoneTypeOptions = [{
      label: '座机',
      value: 'phone'
    }, {
      label: '手机',
      value: 'mobile'
    }];
    return {
      storeData1: {
        nickName: '李云腾',
        sex: '女',
        married: false,
        marryItems: [{
          value: true, label: '已婚'
        }, {
          value: false, label: '未婚'
        }],
        sexItems: [{
          value: '-', label: '不告诉你'
        }, {
          value: '男', label: '男'
        }, {
          value: '女', label: '女'
        }],
        habit: habit,
        habitItems: habitItems,
        birthday: new Date(),
        avatar: [{ src: 'https://www.baidu.com/img/bd_logo1.png' }]
      },
      storeData2: {
        lastName: '',
        firstName: '',
        habit: habit,
        habitItems: habitItems,
        phone: '18888888888',
        phoneType: phoneType,
        phoneTypeOptions: phoneTypeOptions,
        start: null,
        end: null
      },
      storeData3: {
        statusSelected: 1,
        statusOptions: [{
          label: '状态1',
          value: 1
        }, {
          label: '状态2',
          value: 2
        }, {
          label: '状态3',
          value: 3
        }, {
          label: '状态4',
          value: 4
        }],
        range: [moment().toDate(), moment().add(3, 'month').toDate()]
      },
      storeData4: {
        name: '',
        age: null,
        desc: '',
        habit: habit,
        habitItems: habitItems,
        phone: '18888888888',
        phoneType: phoneType,
        phoneTypeOptions: phoneTypeOptions,
        start: null,
        end: null,
        protocol: ''
      },
      requiredRule: [{
        name: 'required',
        value: true,
        triggers: 'blur,input'
      }],
      numRequiredRule: [{
        name: 'numeric',
        value: true,
        triggers: 'blur,input'
      }, {
        name: 'required',
        value: true,
        triggers: 'blur,input'
      }],
      protocolRequiredRule: [{
        name: 'required',
        value: true,
        message: '请勾选阅读协议',
        triggers: 'change'
      }],
      dynamicNameRule: [{
        name: 'required',
        value: true,
        triggers: 'blur,input'
      }, {
        name: 'minLength',
        value: 2
      }],
      ageRule: [{
        name: 'required',
        value: true,
        triggers: 'blur,input'
      }, {
        name: 'numeric',
        value: true,
        triggers: 'blur,input'
      }, {
        name: 'maxLength',
        value: 3,
        triggers: 'change'
      }],
      habitRule: [{
        name: 'minLength',
        value: 3,
        message: '至少选择三个爱好',
        triggers: 'change'
      }],
      isValidating: false,
      validators: [{
        fields: ['start', 'end'],
        handler: function handler(start, end) {
          if (start == null || end == null) {
            return true;
          }

          if (parseInt(start, 10) >= parseInt(end, 10)) {
            return {
              start: '下限必须小于上限'
            };
          }
          return true;
        },

        triggers: ['change', 'submit,input']
      }, {
        fields: ['phone'],
        handler: function handler(phone) {
          return new Promise(function (resolve) {
            setTimeout(function () {
              var res = void 0;
              if (phone === '18888888888') {
                res = {
                  phoneSet: '该手机已被注册'
                };
              }
              return resolve(res);
            }, 3000);
          });
        }
      }],
      beforeValidate: function beforeValidate() {
        bus.$emit('log', 'beforeValidate');
        this.isValidating = true;
      },
      afterValidate: function afterValidate() {
        bus.$emit('log', 'afterValidate');
        this.isValidating = false;
      },

      storeData5: {
        qindian: 'Evan You',
        scheduleInfo: [{
          project: 'vuejs',
          range: [moment().toDate(), moment().add(3, 'month').toDate()]
        }]
      },
      qindianValidator: [{
        fields: ['qindian', 'qindian'],
        handler: function handler(qindian) {
          if (qindian !== 'Evan You') {
            return {
              qindian: '该项为钦点项，就别改了'
            };
          }
          return true;
        },

        triggers: ['input', 'submit']
      }]
    };
  },


  methods: {
    handleInvalid: function handleInvalid(e) {
      bus.$emit('log', 'handleInvalid', e);
      this.isValidating = false;
    },
    submit: function submit(data, e) {
      bus.$emit('log', 'submit', data, e);
    },
    dynamicAdd: function dynamicAdd() {
      this.storeData5.scheduleInfo.push({
        project: null,
        range: null
      });
    },
    dynamicDelete: function dynamicDelete(index) {
      this.storeData5.scheduleInfo.splice(index, 1);
    }
  }
};


