

import { Calendar } from 'veui';

import 'veui-theme-dux/components/calendar.less';
export default {
  name: 'calendar-demo',
  components: {
    'veui-calendar': Calendar
  },
  data: function data() {
    var today = new Date();
    return {
      selected0: null,
      selected1: new Date(1987, 6, 11),
      selected2: [today, new Date(today.getFullYear(), today.getMonth() + 1, 13)],
      selected3: [[new Date(2017, 3, 12), new Date(2017, 3, 18)], [new Date(2017, 3, 22), new Date(2017, 3, 24)]],
      isDisabled: function isDisabled(day) {
        return !(day.getDate() % 5);
      },
      dateClass: function dateClass(day) {
        if ((day.getDay() + 1) % 7 < 2) {
          return {
            'veui-calendar-weekend': true
          };
        }
        return {};
      }
    };
  }
};


