import _uniqueId from 'lodash/uniqueId';


import { input } from '../mixins';
import Radio from './Radio';
import 'veui-theme-dux/components/radio.less';


export default {
  name: 'veui-radio-group',
  components: {
    'radio': Radio
  },
  mixins: [input],
  model: {
    event: 'change'
  },
  props: {
    ui: String,
    items: Array,
    value: null
  },
  computed: {
    localName: function localName() {
      return this.realName || _uniqueId('veui-radio-group-');
    }
  },
  methods: {
    handleChange: function handleChange(value, checked) {
      if (checked) {
        this.$emit('change', value);
      }
    }
  }
};


