import _isArray from 'lodash/isArray';


import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';


var TYPE_MAP = {
  success: 'check-circle',
  warning: 'exclamation-circle',
  info: 'info-circle',
  error: 'cross-circle'
};

export default {
  name: 'alert',
  components: {
    'veui-icon': Icon
  },
  props: {
    ui: String,
    type: {
      type: String,
      default: 'success'
    },
    message: [String, Array],
    closeText: String,
    open: {
      type: Boolean,
      default: true
    }
  },
  data: function data() {
    return {
      localOpen: this.open,
      index: 0
    };
  },

  watch: {
    open: function open(value) {
      this.localOpen = value;
    }
  },
  computed: {
    iconName: function iconName() {
      return TYPE_MAP[this.type];
    },
    isMultiple: function isMultiple() {
      return _isArray(this.message);
    },
    isFirst: function isFirst() {
      return this.index <= 0;
    },
    isLast: function isLast() {
      return this.index >= this.message.length - 1;
    }
  },
  methods: {
    close: function close() {
      this.localOpen = false;
      this.$emit('update:open', false);
    },
    switchMessage: function switchMessage(step) {
      if (step > 0 && this.isLast || step < 0 && this.isFirst) {
        return;
      }
      this.index = this.index + step;
    }
  }
};


