

import bus from '../bus';
import { Tooltip, Button, Input } from 'veui';

import 'veui-theme-dux/components/input.less';
import 'veui-theme-dux/components/button.less';
import 'veui-theme-dux/components/tooltip.less';
export default {
  name: 'tooltip-demo',
  data: function data() {
    return {
      position: '',
      clickPosition: '',
      ui: '',
      target: 'topLeftHover',
      clickTarget: 'topLeftClick',
      open: false,
      clickOpen: false,
      number: '12345678910987654321',
      numberOpen: false
    };
  },

  components: {
    'veui-button': Button,
    'veui-tooltip': Tooltip,
    'veui-input': Input
  },
  mounted: function mounted() {
    this.$children.forEach(function (child) {
      child.$on('click', function () {
        bus.$emit('log', child.$el.getAttribute('ui'));
      });
    });
  },

  methods: {
    show: function show(obj) {
      this.position = obj.position;
      this.target = obj.target;
      this.open = true;
    },
    clickShow: function clickShow(obj) {
      this.clickPosition = obj.clickPosition;
      this.clickTarget = obj.clickTarget;
      this.clickOpen = true;
    }
  }
};


