import _toConsumableArray from 'babel-runtime/helpers/toConsumableArray';
import _includes from 'lodash/includes';
import _find from 'lodash/find';
import _pick from 'lodash/pick';

import Dialog from './Dialog';
import 'veui-theme-dux/components/dialog.less';
import Button from './Button';
import 'veui-theme-dux/components/button.less';
import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';
import config from '../managers/config';

config.defaults({
  'alertbox.priority': 100
});

export default {
  name: 'veui-alert-box',
  props: _pick(Dialog.props, ['open', 'title', 'ui']),
  components: {
    'veui-dialog': Dialog,
    'veui-button': Button,
    'veui-icon': Icon
  },
  data: function data() {
    return {
      localOpen: this.open,
      priority: config.get('alertbox.priority')
    };
  },

  computed: {
    uis: function uis() {
      return [].concat(_toConsumableArray((this.ui || '').split(/\s+/)), ['reverse']);
    },
    localUi: function localUi() {
      return this.uis.join(' ');
    },
    type: function type() {
      return _find(this.uis, function (ui) {
        return _includes(['success', 'error', 'info'], ui);
      });
    },
    typeIconName: function typeIconName() {
      return {
        success: 'check-circle',
        info: 'info-circle',
        error: 'cross-circle'
      }[this.type];
    }
  },
  watch: {
    open: function open(v) {
      this.localOpen = v;
    },
    localOpen: function localOpen(v) {
      this.$emit('update:open', v);
    }
  },
  methods: {
    ok: function ok() {
      this.$emit('ok');
    }
  }
};


