import _includes from 'lodash/includes';


import { input, ui } from '../mixins';

import Button from './Button';

import 'veui-theme-dux/components/button.less';
export default {
  name: 'veui-radio-button-group',
  components: {
    'veui-button': Button
  },
  mixins: [input, ui],
  model: {
    event: 'change'
  },
  props: {
    ui: String,
    items: Array,
    value: null
  },
  computed: {
    buttonUI: function buttonUI() {
      var allowed = ['alt', 'tiny', 'small', 'large'];
      return this.uiProps.filter(function (ui) {
        return _includes(allowed, ui);
      }).join(' ');
    }
  },
  methods: {
    handleChange: function handleChange(val) {
      if (this.val !== val) {
        this.$emit('change', val);
      }
    }
  }
};


