

export default {
  name: 'veui-link',
  components: {
    'veui-element': Element
  },
  props: {
    to: {
      type: String,
      default: ''
    },
    replace: {
      type: Boolean,
      default: false
    },
    native: {
      type: Boolean,
      default: false
    },
    fallback: {
      type: String,
      default: 'span'
    }
  },
  methods: {
    handleRedirect: function handleRedirect(event) {
      if (this.to) {
        this.$emit('click', event);

        if (this.replace && !event.defaultPrevented) {
          event.preventDefault();
          location.replace(this.to);
        }
      } else {
        event.preventDefault();
        this.$emit('click', event);
      }
    }
  }
};


