

import { Overlay, Button } from 'veui';

import 'veui-theme-dux/components/button.less';
import 'veui-theme-dux/components/overlay.less';
export default {
  name: 'overlay-demo',
  components: {
    'veui-overlay': Overlay,
    'veui-button': Button
  },
  data: function data() {
    var items = [{
      name: 'John',
      age: 18
    }, {
      name: 'Joe',
      age: 19
    }, {
      name: 'Amy',
      age: 20
    }];
    return {
      overlayVisible: false,

      multiTargetRef: 'overlay3',
      multiTargetOpen: false,
      multiOptions: {
        attachment: 'bottom left',
        targetAttachment: 'top left'
      },

      vforItems: items,
      vforOpen: false,
      vforCurrentItem: items[0],
      vforTargetRef: 'overlay5-0',
      vnodeTarget: null,
      vnodeComponentTarget: null
    };
  },
  mounted: function mounted() {
    this.vnodeTarget = this.$refs.vnodeTest;
    this.vnodeComponentTarget = this.$refs.vnodeComponentTest;
  },

  methods: {
    showMultiFirst: function showMultiFirst() {
      this.multiTargetOpen = true;
      this.multiTargetRef = 'overlay3';
    },
    showMultiSecond: function showMultiSecond() {
      this.multiTargetOpen = true;
      this.multiTargetRef = 'overlay4';
    },
    showItem: function showItem(item, index) {
      this.vforOpen = true;
      this.vforCurrentItem = item;
      this.vforTargetRef = 'overlay5-' + index;
    }
  }
};


