

import bus from '../bus';
import { Searchbox, Icon } from 'veui';

import 'veui-theme-dux/components/icon.less';
import 'veui-theme-dux/components/searchbox.less';
export default {
  name: 'demo-searchbox',
  components: {
    'veui-searchbox': Searchbox,
    Icon: Icon
  },
  data: function data() {
    return {
      name: 'name',
      value: '测试值',
      value2: '测试值',
      placeholder: '百度(placeholder)',
      suggestions1: [],
      suggestions2: [],
      suggestions3: [],
      suggestions4: [],
      suggestions5: [],
      suggestions6: [],
      suggestions7: []
    };
  },

  methods: {
    handleInput: function handleInput(num, value) {
      console.log(num, value);
      if (value && num < 3) {
        this['suggestions' + num] = [{
          value: value,
          label: value
        }, {
          value: '百度',
          label: '百度'
        }, {
          value: '百度贴吧',
          label: '百度贴吧'
        }, {
          value: '百度MVP',
          label: '百度MVP'
        }];
      } else if (value) {
        this['suggestions' + num] = [value, '百度', '百度贴吧', '百度MVP', '百度指数'];
      } else {
        this['suggestions' + num] = null;
      }
    },
    log: function log(item) {
      bus.$emit('log', item);
    }
  }
};


