

import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';

var ICON_MAP = {
  success: 'check',
  warning: 'exclamation',
  info: 'info',
  error: 'cross'
};

export default {
  name: 'toast',
  components: {
    'veui-icon': Icon
  },
  props: {
    ui: String,
    type: {
      type: String,
      default: 'success'
    },
    message: String,
    duration: {
      type: Number,
      default: 3000
    }
  },
  computed: {
    iconName: function iconName() {
      return ICON_MAP[this.type];
    }
  },
  mounted: function mounted() {
    var _this = this;

    this.timer = setTimeout(function () {
      _this.$emit('close');
    }, this.duration);
  },
  beforeDestroy: function beforeDestroy() {
    clearTimeout(this.timer);
  }
};


