import _extend from 'lodash/extend';
import _pick from 'lodash/pick';


import Input from './Input';
import 'veui-theme-dux/components/input.less';
import Dialog from './Dialog';
import 'veui-theme-dux/components/dialog.less';

import config from '../managers/config';

config.defaults({
  'promptbox.priority': 100
});

export default {
  name: 'veui-prompt-box',
  components: {
    'veui-input': Input,
    'veui-dialog': Dialog
  },
  props: _extend(_pick(Dialog.props, ['open', 'title', 'ui']), {
    content: {
      type: String,
      default: '请输入'
    },
    value: {
      type: String,
      default: ''
    }
  }),
  data: function data() {
    return {
      localOpen: this.open,
      priority: config.get('promptbox.priority'),
      localValue: this.value
    };
  },

  watch: {
    open: function open(v) {
      this.localOpen = v;
    },
    value: function value(v) {
      this.localValue = v;
    },
    localOpen: function localOpen(v) {
      this.$emit('update:open', v);
    },
    localValue: function localValue(v) {
      this.$emit('input', v);
    }
  }
};


