import _omit from 'lodash/omit';

import Icon from './Icon';
import 'veui-theme-dux/components/icon.less';
import '../icons';

export default {
  name: 'veui-button',
  components: {
    Icon: Icon
  },
  props: {
    ui: String,
    disabled: Boolean,
    name: String,
    type: {
      type: String,
      default: 'button'
    },
    value: String,
    autofocus: Boolean,
    loading: Boolean
  },
  computed: {
    attrs: function attrs() {
      var attrs = _omit(this.$props, 'loading');
      attrs.disabled = this.disabled || this.loading;
      return attrs;
    }
  }
};


